.services {
  background-color: $dark;
  padding: 88px 0 128px;

  @media (max-width: 1023px) {
    padding: 52px 0 80px 0;
  }

  @media (max-width: 767px) {
    padding: 52px 0 69px 0;
  }
}

.services__wrapper {
  padding: 0 19% 0;
  width: 100%;
  transform: translate(10px, 4px);

  @media (max-width: 1023px) {
    padding: 0 3% 0;
    transform: translate(0, 0);
  }

  @media (max-width: 767px) {
    padding: 0 8% 0;
  }
}

.services__wrapper h2 {
  display: block;
  margin: 0 0 53px 0;
  padding: 0;
  font-size: 36px;
  line-height: 51px;
  color: $white;
  transform: translate(-1px, -4px);

  @include roboto-regular;

  @media (max-width: 1023px) {
    margin: 0 0 50px;
    font-size: 32px;
    line-height: 45px;
    width: 86%;
    transform: translate(-3px, 0);
  }

  @media (max-width: 767px) {
    font-size: 28px;
    line-height: 40px;
    width: 100%;
    transform: translate(-1px, -3px);
    margin: 0 0 35px;
  }
}

.services__wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;

  @media (max-width: 1023px) {
    flex-direction: column;
    width: 100%;
    gap: 46px;
  }

  @media (max-width: 767px) {
    gap: 29px;
  }
}

.services__wrapper li {
  @media (max-width: 1023px) {
    width: 100%;
  }
}

.services__wrapper img {
  @media (max-width: 1023px) {
    width: 100%;
    height: 256px;
  }
}

.services__wrapper a {
  display: block;
  width: 370px;
  height: 620px;
  position: relative;

  @media (max-width: 1023px) {
    width: 100%;
    height: 256px;
  }
}

.services__wrapper a:hover {
  box-shadow: 0.4em 0.4em 5px rgba(122, 122, 122, 0.5);
}

.services__wrapper a::after {
  content: "";
  display: block;
  position: absolute;
  width: 63px;
  height: 63px;
  background-color: $blue;
  bottom: 0;
  right: 0;
  background-image: url("../img/icon-forward.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.services__wrapper span {
  display: block;
  max-width: 170px;
  position: absolute;
  top: 46px;
  left: 43px;
  font-size: 20px;
  line-height: 30px;
  color: $white;

  @include roboto-regular;

  @media (max-width: 767px) {
    top: 24px;
    left: 24px;
  }
}
